code[class*="language-"],
pre[class*="language-"] {
    color: #f8f8f2;
    background: 0 0;
    text-shadow: 0 1px rgba(0, 0, 0, 0.3);
    font-family: Consolas, Monaco, Andale Mono, Ubuntu Mono, monospace;
    font-size: 1em;
    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    line-height: 1.5;
    -moz-tab-size: 4;
    -o-tab-size: 4;
    tab-size: 4;
    -webkit-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;
}
pre[class*="language-"] {
    padding: 1em;
    margin: 0.5em 0;
    overflow: auto;
    border-radius: 0.3em;
}
:not(pre) > code[class*="language-"],
pre[class*="language-"] {
    background: #272822;
}
:not(pre) > code[class*="language-"] {
    padding: 0.1em;
    border-radius: 0.3em;
    white-space: normal;
}
.token.cdata,
.token.comment,
.token.doctype,
.token.prolog {
    color: #8292a2;
}
.token.punctuation {
    color: #f8f8f2;
}
.token.namespace {
    opacity: 0.7;
}
.token.constant,
.token.deleted,
.token.property,
.token.symbol,
.token.tag {
    color: #f92672;
}
.token.boolean,
.token.number {
    color: #ae81ff;
}
.token.attr-name,
.token.builtin,
.token.char,
.token.inserted,
.token.selector,
.token.string {
    color: #a6e22e;
}
.language-css .token.string,
.style .token.string,
.token.entity,
.token.operator,
.token.url,
.token.variable {
    color: #f8f8f2;
}
.token.atrule,
.token.attr-value,
.token.class-name,
.token.function {
    color: #e6db74;
}
.token.keyword {
    color: #66d9ef;
}
.token.important,
.token.regex {
    color: #fd971f;
}
.token.bold,
.token.important {
    font-weight: 700;
}
.token.italic {
    font-style: italic;
}
.token.entity {
    cursor: help;
}
pre[class*="language-"].line-numbers {
    position: relative;
    padding-left: 3.8em;
    counter-reset: linenumber;
}
pre[class*="language-"].line-numbers > code {
    position: relative;
    white-space: inherit;
}
.line-numbers .line-numbers-rows {
    position: absolute;
    pointer-events: none;
    top: 0;
    font-size: 100%;
    left: -3.8em;
    width: 3em;
    letter-spacing: -1px;
    border-right: 1px solid #999;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.line-numbers-rows > span {
    display: block;
    counter-increment: linenumber;
}
.line-numbers-rows > span:before {
    content: counter(linenumber);
    color: #999;
    display: block;
    padding-right: 0.8em;
    text-align: right;
}
.prism-previewer,
.prism-previewer:after,
.prism-previewer:before {
    position: absolute;
    pointer-events: none;
}
.prism-previewer,
.prism-previewer:after {
    left: 50%;
}
.prism-previewer {
    margin-top: -48px;
    width: 32px;
    height: 32px;
    margin-left: -16px;
    z-index: 10;
    opacity: 0;
    transition: opacity 0.25s;
}
.prism-previewer.flipped {
    margin-top: 0;
    margin-bottom: -48px;
}
.prism-previewer:after,
.prism-previewer:before {
    content: "";
    position: absolute;
    pointer-events: none;
}
.prism-previewer:before {
    top: -5px;
    right: -5px;
    left: -5px;
    bottom: -5px;
    border-radius: 10px;
    border: 5px solid #fff;
    box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.5), 0 0 10px rgba(0, 0, 0, 0.75);
}
.prism-previewer:after {
    top: 100%;
    width: 0;
    height: 0;
    margin: 5px 0 0 -7px;
    border: 7px solid rgba(255, 0, 0, 0);
    border-top-color: #fff;
}
.prism-previewer.flipped:after {
    top: auto;
    bottom: 100%;
    margin-top: 0;
    margin-bottom: 5px;
    border-top-color: rgba(255, 0, 0, 0);
    border-bottom-color: #fff;
}
.prism-previewer.active {
    opacity: 1;
}
.prism-previewer-angle:before {
    border-radius: 50%;
    background: #fff;
}
.prism-previewer-angle:after {
    margin-top: 4px;
}
.prism-previewer-angle svg {
    width: 32px;
    height: 32px;
    transform: rotate(-90deg);
}
.prism-previewer-angle[data-negative] svg {
    transform: scaleX(-1) rotate(-90deg);
}
.prism-previewer-angle circle {
    fill: transparent;
    stroke: #2d3438;
    stroke-opacity: 0.9;
    stroke-width: 32;
    stroke-dasharray: 0, 500;
}
.prism-previewer-gradient {
    background-image: linear-gradient(
            45deg,
            #bbb 25%,
            transparent 0,
            transparent 75%,
            #bbb 0,
            #bbb
        ),
        linear-gradient(45deg, #bbb 25%, #eee 0, #eee 75%, #bbb 0, #bbb);
    background-size: 10px 10px;
    background-position: 0 0, 5px 5px;
    width: 64px;
    margin-left: -32px;
}
.prism-previewer-gradient:before {
    content: none;
}
.prism-previewer-gradient div {
    position: absolute;
    top: -5px;
    left: -5px;
    right: -5px;
    bottom: -5px;
    border-radius: 10px;
    border: 5px solid #fff;
    box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.5), 0 0 10px rgba(0, 0, 0, 0.75);
}
.prism-previewer-color {
    background-image: linear-gradient(
            45deg,
            #bbb 25%,
            transparent 0,
            transparent 75%,
            #bbb 0,
            #bbb
        ),
        linear-gradient(45deg, #bbb 25%, #eee 0, #eee 75%, #bbb 0, #bbb);
    background-size: 10px 10px;
    background-position: 0 0, 5px 5px;
}
.prism-previewer-color:before {
    background-color: inherit;
    background-clip: padding-box;
}
.prism-previewer-easing {
    margin-top: -76px;
    margin-left: -30px;
    width: 60px;
    height: 60px;
    background: #333;
}
.prism-previewer-easing.flipped {
    margin-bottom: -116px;
}
.prism-previewer-easing svg {
    width: 60px;
    height: 60px;
}
.prism-previewer-easing circle {
    fill: #2d3438;
    stroke: #fff;
}
.prism-previewer-easing path {
    fill: none;
    stroke: #fff;
    stroke-linecap: round;
    stroke-width: 4;
}
.prism-previewer-easing line {
    stroke: #fff;
    stroke-opacity: 0.5;
    stroke-width: 2;
}
@-webkit-keyframes prism-previewer-time {
    0% {
        stroke-dasharray: 0, 500;
        stroke-dashoffset: 0;
    }
    50% {
        stroke-dasharray: 100, 500;
        stroke-dashoffset: 0;
    }
    to {
        stroke-dasharray: 0, 500;
        stroke-dashoffset: -100;
    }
}
@keyframes prism-previewer-time {
    0% {
        stroke-dasharray: 0, 500;
        stroke-dashoffset: 0;
    }
    50% {
        stroke-dasharray: 100, 500;
        stroke-dashoffset: 0;
    }
    to {
        stroke-dasharray: 0, 500;
        stroke-dashoffset: -100;
    }
}
.prism-previewer-time:before {
    border-radius: 50%;
    background: #fff;
}
.prism-previewer-time:after {
    margin-top: 4px;
}
.prism-previewer-time svg {
    width: 32px;
    height: 32px;
    transform: rotate(-90deg);
}
.prism-previewer-time circle {
    fill: transparent;
    stroke: #2d3438;
    stroke-opacity: 0.9;
    stroke-width: 32;
    stroke-dasharray: 0, 500;
    stroke-dashoffset: 0;
    -webkit-animation: prism-previewer-time 3s linear infinite;
    animation: prism-previewer-time 3s linear infinite;
}
div.code-toolbar {
    position: relative;
}
div.code-toolbar > .toolbar {
    position: absolute;
    z-index: 10;
    top: 0.3em;
    right: 0.2em;
    transition: opacity 0.3s ease-in-out;
    opacity: 0;
}
div.code-toolbar:hover > .toolbar {
    opacity: 1;
}
div.code-toolbar:focus-within > .toolbar {
    opacity: 1;
}
div.code-toolbar > .toolbar > .toolbar-item {
    display: inline-block;
}
div.code-toolbar > .toolbar > .toolbar-item > a {
    cursor: pointer;
}
div.code-toolbar > .toolbar > .toolbar-item > button {
    background: 0 0;
    border: 0;
    color: inherit;
    font: inherit;
    line-height: normal;
    overflow: visible;
    padding: 0;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}
div.code-toolbar > .toolbar > .toolbar-item > a,
div.code-toolbar > .toolbar > .toolbar-item > button,
div.code-toolbar > .toolbar > .toolbar-item > span {
    color: #bbb;
    font-size: 0.8em;
    padding: 0 0.5em;
    background: #f5f2f0;
    background: hsla(0, 0%, 87.8%, 0.2);
    box-shadow: 0 2px 0 0 rgba(0, 0, 0, 0.2);
    border-radius: 0.5em;
}
div.code-toolbar > .toolbar > .toolbar-item > a:focus,
div.code-toolbar > .toolbar > .toolbar-item > a:hover,
div.code-toolbar > .toolbar > .toolbar-item > button:focus,
div.code-toolbar > .toolbar > .toolbar-item > button:hover,
div.code-toolbar > .toolbar > .toolbar-item > span:focus,
div.code-toolbar > .toolbar > .toolbar-item > span:hover {
    color: inherit;
    text-decoration: none;
}

